import React from 'react'
import './yo.css'

function Yo(props) {
	return(
		<div id="Yo">
			<div id="title" className="child">
				<h1>Junior Freelance</h1>
			</div>

			<div id="perfil" className="child">
				<img src={props.datos.src} id="ip"/>
				<div id="info">
					<h3>Nombre: {props.datos.info.nombre}</h3>
					<h3>Edad: {props.datos.info.edad}</h3>
					<h3>Locacin: {props.datos.info.loca}</h3>
					<h3>Ocupacin: {props.datos.info.ocu}</h3>
				</div>
			</div>

			<div id="certificados" className="child">
				<h3>Sobre m</h3>
				<p>{props.datos.info.mi}</p>
			</div>
		</div>
	)
}

export default Yo


