import React, { Component } from 'react'
import PresentationLayout from '../components/presentation-layout'

export default class Presentation extends Component {
	render() {
		return(
			<PresentationLayout 
				src={this.props.src} 
				name={this.props.name} 
				texto={this.props.texto}
			/>
		)
	}
}


