import React, { Component } from 'react';
import AppLayout from '../components/app-layout'
import Target from '../../target/containers/target'
import Portal from '../../portal/containers/portal'
import Yo from '../components/yo'
import Portafolio from '../components/portafolio'
import Resenas from '../components/resenas'

class App extends Component {
	state = {
		name: "Edisson Mata",
		texto: "Desarrollador Web",
		handleModal: false,
		handleIModal: false,
		handlePortafolyModal: false,
		handleWorkModal: false,
		datos: this.props.datos
	}
	handleOpenIModal = (event) => {
		 this.setState({
			handleModal: true,
			handleIModal: true
		})
	}
	handleOpenPortafolyModal = (event) => {
		this.setState({
			handleModal: true,
			handlePortafolyModal: true
		})
	}
	handleOpenWorkModal = (event) => {
		this.setState({
			handleModal: true,
			handleWorkModal: true
		})
	}
	handleExitModal = (event) => {
		this.setState({
			handleModal: false,
			handleIModal: false,
			handlePortafolyModal: false,
			handleWorkModal: false
		})
	}
	render() {
		return (
			<AppLayout>
				<Target 
					src={this.state.src} 
					name={this.state.name} 
					texto={this.state.texto}
					handleOpenIModal={this.handleOpenIModal}
					handleOpenWorkModal={this.handleOpenWorkModal}
					handleOpenPortafolyModal={this.handleOpenPortafolyModal}
				/>
				{
					this.state.handleModal && 
					<Portal>
						<div id="Portal">
							<button onClick={this.handleExitModal}><i className="icon-cancel-circled"></i></button>

							{
								this.state.handleIModal &&
								<Yo datos={this.state.datos.yo}/>
							}

							{
								this.state.handlePortafolyModal &&
								<Portafolio datos={this.state.datos.portafoly}/>
							}

							{
								this.state.handleWorkModal &&
								<Resenas datos={this.state.datos.resenas}/>
							}
						</div>
					</Portal>
				}
			</AppLayout>
		)
	}
}

export default App


