import React, { Component } from 'react';
import SliderPlayer from '../components/slider-layout'
import Options from '../components/options'

export default class Slider extends Component {
	render() {
		return(
			<SliderPlayer>
			<h3>Datos personales</h3>
				<Options 
					handleOpenIModal={this.props.handleOpenIModal}
					handleOpenWorkModal={this.props.handleOpenWorkModal}
					handleOpenPortafolyModal={this.props.handleOpenPortafolyModal}
				/>
			</SliderPlayer>
		)
	}
}


