import React, { Component } from 'react'
import './resenas.css'
import Options from './options'

class Resenas extends Component {
	render() {
		return(
			<div id="Resenas">
				<h2>Reseas de Trabajos</h2>
				{
					this.props.datos.map((items) => {
						return <Options  img={items.img} info={items.info} key={items.id}/>
					})
				}
			</div>
		)
	}
}

export default Resenas


