import React from 'react';
import './options.css'
import './fontello.css'

class Options extends React.Component {
	render() {
		return(
			<div id="Options">
				<div className="Options-op" onClick={this.props.handleOpenIModal}>
					<i className="icon-address-card"></i>

					<div className="info">
						<h4>Yo</h4>
					</div>
				</div>

				<div className="Options-op" onClick={this.props.handleOpenPortafolyModal}>
					<i className="icon-fork"></i>

					<div className="info">
						<h4>Portafolio</h4>
					</div>
				</div>

				<div className="Options-op" onClick={this.props.handleOpenWorkModal}>
					<i className="icon-briefcase"></i>

					<div className="info">
						<h4>Reseas</h4>
					</div>
				</div>
			</div>
		)
	}
}

export default Options


