import React, { Component } from 'react';
import TargetLayout from '../components/target-layout'
import Presentation from '../../widgets/containers/presentation'
import Slider from '../../widgets/containers/slider'

class Target extends Component {
	render() {
		return(
			<TargetLayout>
				<Presentation 
					src={this.props.src} 
					name={this.props.name} 
					texto={this.props.texto}	
				/>
				<Slider 
					handleOpenIModal={this.props.handleOpenIModal} 
					handleOpenWorkModal={this.props.handleOpenWorkModal}
					handleOpenPortafolyModal={this.props.handleOpenPortafolyModal}
				/>
			</TargetLayout>
		)
	}
}

export default Target


