import React from 'react';
import './presentation.css'
import img from './perfil.jpeg'


function PresentationLayout(props) {
	return(
		<div id="Presentation">
			<img src={img} />
			<h2>{props.name}</h2>
			<h3>{props.texto}</h3>
		</div>
	)
}

export default PresentationLayout;


